.skill-wrapper {
  display: inline-block;
  width: 33.33%;
  padding-right: 3em;
  text-align: left;
  text-transform: uppercase;
}

.skill-wrapper.splash {
  width: 100%;
  margin-top: 15px;
  padding-right: 0em;
}

.skill-bar {
  position: relative;
  height: 15px;
  max-width: 700px;
  margin-top: 0.5em;
  margin-bottom: 1em;
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
  background-color: #e0ddd5;
}

.skill-progress {
  width: 100%;
  height: 100%;
  background-color: #2cd1a0;
}

.skill-progress.bad {
  background-color: #f4e430;
}

.skill-progress.bad.ph {
  width: 99%;
}

.skill-progress-wrapper {
  overflow: hidden;
  height: 100%;
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
}

.skill-progress-wrapper.business-str {
  width: 95%;
}

.skill-progress-wrapper.business-str.ph {
  width: 56%;
}

.skill-progress-wrapper.info-arch {
  width: 82%;
}

.skill-progress-wrapper.d-strategy {
  width: 95%;
}

.skill-progress-wrapper.d-strategy.bad {
  width: 60%;
}

.skill-progress-wrapper.strategy {
  width: 92%;
}

.skill-progress-wrapper.strategy.ph {
  width: 87%;
}

.circle {
  position: relative;
  display: block;
  overflow: visible;
  width: 150px;
  height: 150px;
  margin-right: auto;
  margin-bottom: 21px;
  margin-left: auto;
  border-radius: 50%;
  border: 5px solid #469bd4;
  background-color: #ede9e1;
  -webkit-perspective: 1000px;
  perspective: 1000px;
  -webkit-transform: perspective(1000px);
  transform: perspective(1000px);
  -webkit-transition: -webkit-transform 200ms ease;
  transition: -webkit-transform 200ms ease;
  transition: transform 200ms ease;
  transition: transform 200ms ease, -webkit-transform 200ms ease;
}

.circle.out-line {
  margin-top: 15%;
}

.amount {
  position: absolute;
  left: 0px;
  top: 50%;
  right: 0px;
  -webkit-transform: translate(0px, -50%);
  -ms-transform: translate(0px, -50%);
  transform: translate(0px, -50%);
  font-family: Montserrat, sans-serif;
  color: #2a262b;
  font-weight: 700;
  text-align: center;
}

.base-grid {
  position: absolute;
  left: 0px;
  bottom: 0px;
  width: 75px;
  height: 75px;
}

.progress-mask {
  position: absolute;
  top: 0px;
  right: 0px;
  z-index: 2;
  display: block;
  width: 75px;
  height: 75px;
  border-bottom-left-radius: 100%;
  background-color: #cfccc4;
}

.dropdown {
  margin-right: 0px;
  margin-left: 0px;
}

.dropdown-toggle {
  padding-top: 15px;
  padding-bottom: 15px;
}
